import { MyUI } from "@macashipo/mlib"
import React, { Component, Fragment } from "react"
import MyTableHeaderExt from "./_ext"

class MTHhtml extends MyTableHeaderExt {
  constructor(props) {
    super(props)
    this.state = {}
  }

  render() {
    const { headerObj, header } = this.props
    let _style = this.getStyle(this.props)
    return (
      <MyUI
        type="html"
        html={headerObj.html}
        className="mth-html"
        style={_style}
      ></MyUI>
    )
  }
}
export default MTHhtml
